@use "../abstracts/02-color-vars" as col;
@use "../abstracts/01-mixins" as mix;
@use"../core/03-fonts-vars" as ff;

.nav-bk-color {

	background-color: rgba(255, 255, 255, 1) !important;
	//transform: translateY(0);
	transition: background-color 250ms ease-in-out;
	box-shadow: 1px 1px 1px 1px rgba(228, 110, 84, 0.2);
	
	

	// @include mix.media(900px){
	// 	padding-top: 15px;
	// //padding-bottom: 85px;
	// //margin-bottom: 10px;
	// }
	
}

.nav-container {
	grid-area: nav;
	max-width: 100%;
	height: max-content;
	position: sticky;
	top: 0;
	z-index: 4;

	//background-color: palegreen;
	@include mix.media(768px) {
		height: 94.2px;
		}

	@include mix.media(375px){
		height: 100px;
	}
}

 .logo  {
	padding: 15px;
	position: absolute;
	top: 0;
	z-index: 50;

}

.font2{
	font-family: ff.$ffm-logo ;
	position: relative;
	color: black;
	padding-bottom: 8px;

	@include mix.media(768px) {
		font-size: 12px;
		top: 32px;
		left: 5px;
	}
}

.font3{
	font-family: ff.$ffm-logo;
	position: relative;
	color: black;

	@include mix.media(768px) {
		font-size: 14px ;
		top: 38px;
		left: 5px;
	}
}

.nav-wrapper {	 
	
	@include mix.ul-style(none, 4px);
//	width: 99%;
	//	background-color: rgb(255, 255, 255);
	@include mix.media(768px) {
		transform: translateY(-5000px);
		transition: transform 100ms ease;
		//	background-color: rgb(15, 0, 128);
	}

//    @include mix.media(615px){
// 	position: sticky !important;

//    }


}
.nav-wrapper :nth-child(3) {
	margin-left: 50px;
	@include mix.media(768px) {
		margin-left: 0;
	}
}

.nav-item {

	padding: 12px;
}

.nav-link {
	@include mix.link-remove-default(none, 4px);

	@include mix.links(
		col.$secondary-color-nav,
		col.$primary-color-a-hover,
		col.$primary-color-a-active
	);
}
