@use "../abstracts/01-mixins" as mix;

.info-section {
	grid-area: info;

	width: 100%;
	height: max-content;
	background-color: rgba(37, 35, 36, 1);
}

.info-container {
	width: 100%;
	height: max-content;
	background-image: url("data:image/svg+xml,%3Csvg width='325' height='-300' viewBox=' -60 -5 200 200' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Polygon 7'%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='white'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='black'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' stroke='white'/%3E%3C/g%3E%3C/svg%3E%0A");
	background-repeat: no-repeat;
	background-position: center;
	background-size: auto auto;

	@include mix.media(976px) {
		background-image: url("data:image/svg+xml,%3Csvg width='325' height='-300' viewBox=' -60 -5 200 200' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Polygon 7'%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='white'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='black'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' stroke='white'/%3E%3C/g%3E%3C/svg%3E%0A");
	}

	@include mix.media(807px) {
		background-image: url("data:image/svg+xml,%3Csvg width='325' height='-300' viewBox=' -60 -5 200 200' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Polygon 7'%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='white'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='black'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' stroke='white'/%3E%3C/g%3E%3C/svg%3E%0A");
	}
	@include mix.media(768px) {
		background-image: url("data:image/svg+xml,%3Csvg width='325' height='-300' viewBox=' -60 -5 200 200' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cg id='Polygon 7'%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='white'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' fill='black'/%3E%3Cpath d='M0.998322 0.5L39.5 62.0571L78.0017 0.5H0.998322Z' stroke='white'/%3E%3C/g%3E%3C/svg%3E%0A");
	}
}

.info-title-wrapper {
	max-width: 100%;
	height: max-content;
	padding: 50px 0 50px 0;
	color: #eaba32;
}

.info-title {
	padding: 20px;
}

.info-title-wrapper > span {
	background-color: #eaba32;
	margin: 60px;

	width: 175px;
	height: 2px;
}

.info-title-text-wrapper {
	margin-bottom: 65px;
	max-width: 75%;
	padding: 30px;
	//border: 1px solid rgb(245, 242, 242);
	border-radius: 5px;
	//justify-items: left;

	background-image: url("./images/heade-banner-960_720-2.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	background-color: rgba(105, 54, 158, 1);
	//background-color: rgb(240, 230, 230);
	color: goldenrod;
	box-shadow: 1px 1px 1px 1px #111111;

	@include mix.media(900px) {
		max-width: 95%;
		//margin-bottom: 65px;
		padding: 20px;
	}
}

.info-title-text-row {
	width: 90%;
	padding: 30px;
	margin: 0 auto;
	background-color: black;
	border-radius: 5px;

	@include mix.media(568px) {
		max-width: 100%;
		//margin-bottom: 65px;
		padding: 20px;
	}
}

.info-title-text {
	width: 100%;
	margin: 0 auto;
	//color: white;
}

.faded {
	opacity: 0;
	transform: translate3d(0, 5rem, 0);
	transition: transform 1s cubic-bezier(0.64, 0.04, 0.26, 0.87), o;
}

.show {
	opacity: 1;
	transform: translate3d(0, 0, 0);
}

.info-card {
	max-width: 33%;
	max-height: max-content;
	border: 1px solid black;
	//	padding: 10px;
	//background-color: rgba(169, 169, 169, .2);
	background: rgb(2, 0, 36);
	background: linear-gradient(
		342deg,
		rgba(2, 0, 36, 1) 0%,
		rgba(101, 101, 101, 1) 28%
	);
	color: white;

	&:hover {
		background: lighten(rgb(101, 101, 101), 20%);
		color: darken(black, 20%);
		border-radius: 10px;
	}

	@include mix.media(900px) {
		max-width: 45%;
	}

	@include mix.media(568px) {
		max-width: 50%;
	}

	@include mix.media(475px) {
		max-width: 85%;
		border-radius: 5px;
	}
}

.info-card:last-child {
	@include mix.media(900px) {
		max-width: 90%;
	}

	@include mix.media(568px) {
		max-width: 50%;
	}

	@include mix.media(475px) {
		max-width: 85%;
	}
}

.info-title {
	padding: 25px;

	@include mix.media(900px) {
		padding: 25px;
	}
}
