@use "./02-color-vars" as col;

@mixin width($width){
	width: $width;
}

//grid

@mixin grid-col-x($num, $frac) {
	display: grid;
	grid-template-columns: repeat($num, $frac);
}

// pg = page, nv=nav hd=header mn=main ft=footer
@mixin grid-temp-area-x($nv, $hd, $mn, $ft) {
	grid-template-area:
		"$nv $nv $nv $nv"
		"$hd $hd $hd $hd"
		"$mn $mn $mn $mn"
		"$ft $ft $ft $ft";
}

// flex mixins

@mixin flex-column {
	display: flex;
	flex-direction: column;
}

@mixin flex-center {
	display: flex;
	align-items: center;
	justify-content: center;
}

@mixin flex-center-column {
	@include flex-center;
	flex-direction: column;
}

@mixin flex-center-vert {
	display: flex;
	align-items: center;
}

@mixin flex-center-horiz {
	display: flex;
	justify-content: center;
}

@mixin flex-center-vert-horiz {
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: space-evenly;
}
@mixin flex-center-horiz {
	display: flex;
	//	flex-wrap: wrap;
	align-items: center;
	justify-content: space-evenly;
}

// end flex

// background-cover

@mixin cover-background {
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
}

