// nav links styles
@mixin links($link,  $hover, $active) {
	& {
		color: $link;
		&:hover {
			color: $hover;
		}
		&:active,
		&:focus {
			color: $active;
		}
	}
}

// remove default link style
@mixin link-remove-default($txtdec, $spacing) {
	text-decoration: $txtdec;
	padding: $spacing;
}

// end nav links

// ul with padding
@mixin ul-style($lst, $pad) {
	list-style-type: $lst;
	//	display: $blc;
	padding: $pad;
}

@mixin media($width) {
	@media screen and (max-width: $width) {
		@content;
	}
}

@mixin min-media($width) {
	@media screen and (min-width: $width) {
		@content;
	}
}

// for opening nav container .open1 .nav-container
@mixin btn-nav-con() {
	position: relative;
	top: 0;
	width: 100%;


}
//for opening nav wrapper .open1 .nav-wrapper
@mixin btn-nav-wrap() {
	position: absolute;

	top: 0;
	z-index: 5;
	width: 100%;
	transform: top translateY(0);
	transform: translateX(0);
	transition: transform 250ms ease-in;
	flex-direction: column;
	align-items:end;
	padding-right: 100px;
	//justify-content: center;
	background-color: col.$primary-color-fill;
	

	@include media(768px){
		padding-right: 25px;
	}
}

// rotate  span lines .open1 .lines
@mixin rotate-line() {
	transform: rotate(405deg) translate(6px);
	transition: transform 150ms ease-in;
	background-color: col.$primary-color-lines-open;

}

// rotate the line beofre span after click .open1 .lines::before
@mixin rotate-lines-before() {
	opacity: 0;
	transition:  opacity 50ms ease-in;
}

// rotate the line afeter span after click .open1 .lines::after
@mixin rotate-lines-after() {
	transform: rotate(450deg) translate(-6px);
	transition: transform 150ms ease-in;
	background-color: col.$primary-color-lines-open;
}

// change button color on click .open1 .btn1

@mixin button1() {
	color: col.$primary-color-menu-burger;
	transition: transform color 250ms ease-in;
}

@mixin p-font() {
	font-size: clamp(0.875rem, 0.7685rem + 0.4115vw, 1rem);
}

@mixin move-logo(){
	transform: translateY(45px);
	transition: transform 250ms ease-in;

}

