@use "../abstracts/01-mixins" as mix;

.hero-section {
	grid-area: hero;
	max-width: 100%;
	
	height: max-content;
	background-color: white;
}



.hero-header-wrapper{
	grid-area: hero1;
	margin-top: 45px;
	margin-bottom: 15px;
}



.hero-header-text-wrapper {
	grid-area: hero2;
	width: 100%;
	height: auto;
}




.hero-text {
	max-width: 80%;
	padding-top: 25px;
	padding-bottom: 25px;
	//margin-top: 25px;

	/* 	@include mix.media(900px) {
	padding: 20px;
	} */
}

.hero-header-footer-wrapper{
	grid-area: hero3;
	text-align: center;
	margin-top: 25px;
	margin-bottom: 35px;
}

.hero-ul-container {
	grid-area: hero4;
	width: 100%;

background-color: darken(#252324,5%);
color: white;
}

.hero-ul-wrapper {
	width: 100%;
	padding-top: 25px;
	padding-bottom: 25px;
	//background-color: aqua;
	//border: 1px solid red;


}

.hero-li-spacer{
	max-width: 100%;
	padding-top: 15px;
	padding-bottom: 15px;
}

.hero-li {
	max-width: 100%;	
	padding-bottom: 10px;	
	@include mix.media(568px){
		max-width: 80%;
	}

}

.hero-li:nth-child(1){	
	font-size: 17px;
}

.hero-li:nth-child(2){
	font-size: 18px;	
}

.hero-li:nth-child(3){
	font-size: 19px;
}

.hero-li:nth-child(4){
	font-size: 20px;
}

.hero-li:nth-child(5){
	font-size: 21px;
}

.anim{
	opacity: 0;
}

@keyframes fadein {
	from {
		opacity: 0;
	}
    50% {
	opacity: .5;
	transition: opacity 250ms ease-in;
	transform: scale(1.2);
	}
	to {
		opacity: 1;
		transition: opacity 250ms ease-in;
	}
}

