.hero-img-wrapper {
	grid-area: hero5;
	//text-align: center;
	max-width: 100%;
	height: auto;
	//border: 1px solid red;
	padding-top: 20px;
	padding-bottom: 20px;
	transform: rotateY(90deg);
	@include mix.media(568px){
		padding-top: 10px;
	}
}

.hero-title-foot {
	max-width: 100%;
	margin: 50px 0 20px 0;
	@include mix.media(568px){
		margin: 20px 0 20px 0;
	}
}

.hero-img-text{
	max-width: 100%;
	padding-bottom: 20px;	
	@include mix.media(568px){
		text-align: center;
	}
}

.hero-img-container{
	max-width: 100%;
}






@keyframes rotate {
	from {
		opacity: 0;
		transform: rotateY(90deg);
		transition: transform 250ms ease-in;
	}

	to {
		transform: rotateY(0deg);
		transition: transform 250ms ease-in;
		opacity: 1;
	}
}
