.btn-contact{
	width: 150px;
padding: 20px;
	border: 1px solid black;
	background-color: rgb(105, 54, 158);
	border-radius: 0 15px 0 15px;
	color: white;

	&.btn-contact:active {
		background-color: lighten(rgb(105, 54, 158), 20%);
	}

	&.btn-contact:focus {
		background-color: lighten(rgb(105, 54, 158), 20%);
	}



}
