@use "../abstracts/02-color-vars" as col;
@use "../components/02-buttons" as btn;
@use "../core/03-fonts-vars" as ff;
@use"../abstracts/01-mixins" as mix;

footer {
	grid-area: foot;
	
max-width: 100%;
	height: max-content;
	background-color: #f4f7f2;
	color: rgb(6, 6, 6);
}

.font {
	font-family: ff.$ffm-logo;
	font-size: 24px;
}

.font1 {
	font-family: ff.$ffm-logo;
	font-size: 18px;
}

.footer-wrapper {
width: 100%;
height: max-content;
	background-color:white;
	color:black;
}



.f-contact{
	//grid-area: form1;
	width: 90%;
	height: max-content;
	background: rgb(2, 0, 36);
	background: linear-gradient(
		360deg,
		rgba(2, 0, 36, 1) 0%,
		rgba(169, 169, 169, 1) 28%
	);
	padding: 10px 0 10px 0;
	border-radius: 10px;
}


 .form-item {	
	width: 100%;
	height: max-content;
	padding-top: 25px;
	padding-bottom: 25px;
	margin-left: 15px;

}

.placeholder {
	position: absolute;
	top: 15px;
	z-index: 6;
	height: 20px;
	left: 20px;
	width: 85px;
	padding: 12px;
	border-radius: 10px;
	opacity: 0;
	background-color: #eef3f8;
	transform: translateY(0);
	transition: transform 200ms;
	//animation: fadeins .2s ease-in .2s;
	//transition: transform animation .7s ease-in .2s;
	
	
  }
  

.f-label {
	position: absolute;	
	top: 15px;
	left: 22px;
	z-index: 7;
	line-height: 14px;
	pointer-events: none;
	color: #232333;	
	transform-origin: 0 50%;
	transition: transform 200ms, color 200ms;	
}
.input-wrap{
	position: relative;
}

.f-input {
	position: relative;
	max-width: 75%;
	padding: 10px;
	//outline: none;
	border-bottom: 1px solid rgb(10, 9, 9);
	//background-color: rgba(203, 193, 195, 1);
//	border-style: none none solid none;

/* 	@include mix.media(600px) {
		width: 75%;
	} */
}

.messagearea{
	//border: 1px soild black;
	position: relative;
	max-width: 100%;
	border-style: solid none solid none !important;
	@include mix.media(900px){
		max-width: 75%;
	}
}

.form-error {
	padding: 3px;
}

.btn-wrapper{
	//grid-area: form4;
	padding: 15px;

}

.footer-foot {
	padding: 20px;
}


// div for label  placeholder


// placeholder is div for label
.f-input:focus ~ .f-label, 
.f-input:not(:placeholder-shown) ~ .f-label {
  transform: translate(12px, -37px);
}

// // f-label is the input label placeholder is div above label
 .f-input:focus ~ .placeholder,	
	.f-input:not(:placeholder-shown) ~ .placeholder  /* Input has a value */{  
	transform: translate( 10px, -42px)  scale(1.25);
	animation: fadeins .2s ease-in  forwards .2s;
	//transition: transform animation .7s ease-in .2s;
}



// placeholder is div for label
.messagearea:focus ~ .f-label, 
.messagearea:not(:placeholder-shown) ~ .f-label {
  transform: translate(10px, -42px);
 
}

// // messagearea is the textarea.  label placeholder is div above label
 .messagearea:focus ~ .placeholder,	
	.messagearea:not(:placeholder-shown) ~ .placeholder  /* Input has a value */{  
	transform: translate( 10px, -48px)  scale(1.25);
	animation: fadeins .2s ease-in  forwards .2s;
}


.f-input:not(:placeholder-shown) ~ .f-label {
	color: #660866; /* grayish */	
  }
  .f-input:focus ~ .f-label {
	color: #0e0d0e; /* focused red */
  }

  .messagearea:not(:placeholder-shown) ~ .f-label {
	color: #660866; /* grayish */	
  }
  .messagearea:focus ~ .f-label {
	color: #0e0d0e; /* focused red */
  }

 
  @keyframes fadeins {
	0% {
		
		opacity: 0;
	}

	50% {
		
		opacity: .5;
	}

	100% {
		
		opacity: 1;
	}
}
