 @use "../../fonts/stylesheet.css" as *;







// font family

$ffm-body: "Roboto", sans-serif;
$ffm-headings: "Lato", sans-serif;

$ffm-logo: "danielbold", sans-serif;

// line hieght
$lh: 1.43;
// font weight
$fw-200: 200;
$fw-300: 300;
$fw-400: 400;
$fw-500: 500;
$fw-600: 600;
$fw-700: 700;
$fw-800: 800;
$fw-900: 900;

// letter spacing
$text-lp: 0.05em;
// font size and font clamp
$default-fs: 16px;
//$font-clamp-size: clamp(0.875rem, 0.85rem + 0.125vw, 1rem);
