@use "../abstracts/01-mixins" as mix;

body {
	width: 100%;
	height: max-content;

	// background-image: url("../../images/triangles.png");
	// background-repeat: repeat;
	// //background-color: #14226b;
	// background-size: contain;
	// background-position: center;
	//position: relative;
	//overflow-x: hidden;
}

.main-page-container {
	display: grid;
	//grid-auto-columns: 1fr;

	grid-template-areas:
		"nav nav nav nav"
		"header header header header"
		"hero hero hero hero"
		"info info  info info"
		"about about about about"
		"service service service service"
		"foot foot foot foot ";
	//	background-color: green;

	@include mix.media(1026px) {
		grid-template-areas:
			"nav nav nav "
			"header header header "
			"hero hero hero "
			"info info  info"
			"about about about "
			"service service service "
			"foot foot foot";
	}

	@include mix.media(900px) {
		grid-template-areas:
			"nav nav "
			"header header "
			"hero hero "
			"info info "
			"about about "
			"service service"
			"foot foot ";
	}

	@include mix.media(768px) {
		grid-template-areas:
			"nav "
			"header "
			"hero "
			"info "
			"about  "
			"service "
			"foot ";
	}
}

.hero-wrapper{
	display: grid;
	grid-template-areas: 
	"hero1 hero1"
	"hero2 hero2"
	"hero3 hero3"
	"hero4 hero4"
	"hero5 hero5"
	;
	@include mix.media(900px) {
		grid-template-areas:
		"hero1 "
		"hero2 "
		"hero3 "
		"hero4 "
		"hero5 ";
	}

	
}

.about-content-grid-wrapper {
	display: grid;
	grid-template-areas:
	
		"about1 about1"
		"about1 about1";
		justify-items: center;

	@include mix.media(900px) {
		grid-template-areas:
			"about1"
			"about1";
	}
}





.service-main-grid-wrapper {
	display: grid;
	grid-template-areas:
		"service1 service1"
		"service1 service1";

	@include mix.media(900px) {
		grid-template-areas:
			"service1"
			"service1";
	}
}


.f-contact{
	display: grid;
	grid-template-areas:
		"form1 form1"
		"form2 form2"
		"form3 form3"
		//"form4 form4"
		;

		justify-items: center;
		align-items: center;
		//border: 1px solid rgb(9, 1, 7);

		@include mix.media(900px) {
			grid-template-areas:
				"form1"
				"form2"
				"form3"
				"form4"
				"form5"
				"form6";
				justify-items: flex-start;
		}
}
