@use "./03-fonts-vars" as f;



h1 {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-weight: f.$fw-900;
	//  font-size: toRem(24);
	font-family: f.$ffm-headings;
	//font-size: 2rem;
	letter-spacing: f.$text-lp;
	line-height: f.$lh;
	font-size: clamp(1.125rem, 0.7453rem + 1.8987vw, 3rem);
}

h2 {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-weight: f.$fw-900;
	//  font-size: toRem(20);
	font-family: f.$ffm-headings;
	//font-size: 1.4rem;
	letter-spacing: f.$text-lp;
	line-height: f.$lh;
	font-size: clamp(1rem, 0.8987rem + 0.5063vw, 1.5rem);
}

h3 {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-weight: f.$fw-700;
	// font-size: toRem(17);
	font-family: f.$ffm-body;
	//font-size: 1.25rem;
	letter-spacing: f.$text-lp;
	line-height: f.$lh;
	font-size: clamp(1rem, 0.9656rem + 0.1722vw, 1.17rem);
}

h4 {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-weight: f.$fw-400;
	// font-size: toRem(17);
	font-family: f.$ffm-headings;
	font-size: 0.9375rem;
	//font-size: clamp(0.9375rem, 0.9125rem + 0.125vw, 1.0625rem);
}

h5 {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-weight: f.$fw-400;
	// font-size: toRem(17);
	font-family: f.$ffm-headings;
	font-size: 0.9375rem;
	//font-size: clamp(0.9375rem, 0.9125rem + 0.125vw, 1.0625rem);
}

p {
	font-weight: f.$fw-400;
	// font-size: toRem(17);
	font-family: f.$ffm-body;
//	letter-spacing: f.$text-lp;
//	line-height: f.$lh;
font-size: clamp(0.875rem, 0.8497rem + 0.1266vw, 1rem);
}

li{
	font-size: clamp(0.875rem, 0.8497rem + 0.1266vw, 1rem);
}
