@use "./03-fonts-vars" as f;

body,
div,
aside,
main,
nav,
header,
section,
footer,
input,
label,
textarea,
span,
textarea,
p,
ul,
li,
a,
::before,
::after {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-size: f.$default-fs;
}

// reset button defaults
button {
	background: none;
	color: inherit;
	border: none;
	padding: 0;
	font: inherit;
	cursor: pointer;
	outline: inherit;
}

html {
	scroll-behavior: smooth;
	scrollbar-color: red white;
	//outline: 1px solid slategrey;
}


 input,
label,
textarea {
	appearance: none;
	padding: 0;
	background: none;
	border: none;
	border-radius: 0;
	outline: none;
} 

input:focus,
textarea:focus {
	outline: none;
}
