@use"../abstracts/01-mixins" as mix;


.about-section{
grid-area: about;
max-width: 100%;
max-height: max-content;
background-color: white;


}

.about-header-container{
	width: 100%;
	text-align: center;
	padding-top: 75px;
	padding-bottom: 85px;
	margin-bottom: 75px;
	background-color: black;
	color: goldenrod;
	background-image: url("../../images/about-banner-960_720.png");
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;

	@include mix.media(900px){
		padding-top: 15px;
	padding-bottom: 25px;
	
	
	}
	
}

.about-header-overlay{
	width: 80%;
	padding: 25px;
	background-color: rgba(255, 255, 255, .3);
	
	@include mix.media(900px){
		padding: 10px;
	
	}
	
}

.about-header-title{
padding-bottom: 15px;
}

.about-content-grid-wrapper{
	grid-area: about1;
}


.about-card-wrapper{
max-width: 75%;
padding-bottom: 75px;
margin-bottom: 40px;
background-color: rgba(121, 81, 168, .7);
&:hover{
	transform: translate(10px, -13px);
	transition: transform 250ms ease-in;	
	//transform: rotateZ(2deg);
	box-shadow: 1px 1px 2px 2px black;
}

@include mix.media(900px){
	max-width: 50%;
}

@include mix.media(658px){
	max-width: 65%;
}
}

.about-title-text{
	padding: 10px;
}





.about-header-sub{
background-color: rgba(35,35,35, 1);
padding: 10px;
color: white;
text-align: center;
}

.about-title-icon-wrapper{
	width: 100%;
	text-align: center;
	background-color: rgba(35,35,35, 1);
	
}




.about-title-icon{
	
	max-width: 75px;
	max-height: 75px;
	margin-top: -35px;
	padding-bottom: 15px;
}




