.main-page-container {
	margin: 0 auto;
	@include mix.width(80%);
	//min-width: 0px;
	height: max-content;
	background-color: rgba( 152, 152, 152, 1);

	@include mix.media(768px){
		@include mix.width(85%);
	}


	@include mix.media(568px){
		@include mix.width(100%);
	}



